import React, { Component } from 'react';
import * as emailjs from 'emailjs-com';


class Contact extends Component {
    constructor(props) {
        super(props);
        this.state = {
            user_name: '',
            user_email: '',
            user_subject: '',
            user_message: ''
        };

     this.handleSubmit = this.handleSubmit.bind(this);
     this.resetForm = this.resetForm.bind(this);
     this.handleChange = this.handleChange.bind(this);   
    }

    handleSubmit(event) {
    event.preventDefault();
    const { user_name, user_email, user_subject, user_message } = this.state;
    const templateParams = {
      user_name: user_name,
      user_email: user_email,
      user_subject: user_subject,
      user_message: user_message,
    };
    emailjs.send('service_h12vtvl', 'template_ifrglkz', templateParams, 'user_X7ViVVExUYEkRCOOqVUgW')

    this.resetForm();
};

  resetForm() {
    this.setState({
      user_name: '',
      user_email: '',
      user_subject: '',
      user_message: '',
    });
}

  handleChange(event) {
    this.setState({ [event.target.name]: event.target.value });
  }


  render() {

    const { user_name, user_email, user_subject, user_message, sentMessage } = this.state;

    if(this.props.data){
      var name = this.props.data.name;
      var street = this.props.data.address.street;
      var city = this.props.data.address.city;
      var state = this.props.data.address.state;
      var zip = this.props.data.address.zip;
      var phone= this.props.data.phone;
      var email = this.props.data.email;
      var message = this.props.data.contactmessage;
    };

      
        
    return (
      <section id="contact">

         <div className="row section-head">

            <div className="two columns header-col">

               <h1><span>Get In Touch.</span></h1>

            </div>

            <div className="ten columns">

                  <p className="lead">{message}</p>

            </div>

         </div>

      
         <div className="row">
            <div className="eight columns">

                <form onSubmit={this.handleSubmit}>
                 <input type="hidden" name="contact_number" />
               
				<fieldset>

                  <div>
						   <label htmlFor="user_name">Name <span className="required">*</span></label>
						   <input type="text" size="35" name="user_name" value={user_name} onChange={this.handleChange}/>
                  </div>

                  <div>
						   <label htmlFor="user_email">Email <span className="required">*</span></label>
						   <input type="email" size="35" name="user_email" value={user_email} onChange={this.handleChange} />
                  </div>

                  <div>
						   <label htmlFor="user_subject">Subject</label>
						   <input type="text" size="35" name="user_subject" value={user_subject} onChange={this.handleChange}/>
                  </div>

                  <div>
                     <label htmlFor="user_message">Message <span className="required">*</span></label>
                     <textarea cols="50" rows="15" name="user_message" value={user_message} onChange={this.handleChange}></textarea>
                  </div>

                  <div>
                     <button className="submit" type="submit" value="Send" >Submit</button>
                     <span id="image-loader">
                        <img alt="" src="images/loader.gif" />
                     </span>
                  </div>
                  </fieldset>
				   </form>

           <div id="message-warning">Your message has an error.  Please complete required form fields.</div>
				   <div id="message-success">
                  <i className="fa fa-check"></i>Your message was sent.  Thank you!<br />
				   </div>
           </div>     

            <aside className="four columns footer-widgets">
               <div className="widget widget_contact">

					   <h4>Contact</h4>
					   <p className="address">
						   {name}<br />
						   {email}
					   </p>
				   </div>

               <div className="widget widget_tweets">
                  <h4 className="widget-title">Latest Tweets</h4>
                  <ul id="twitter">
                     <li>
                     <blockquote class="twitter-tweet"><p lang="en" dir="ltr">Amazon-Clone-Challenge (FullStack). ReactJS, ContextAPI/Redux, Firebase, express, cors, Stripe, BEM Live demo: <a href="https://t.co/9Ww2Vc5YV4">https://t.co/9Ww2Vc5YV4</a> <a href="https://twitter.com/cleverqazi?ref_src=twsrc%5Etfw">@cleverqazi</a></p>&mdash; Yvette Ingram (@MsYvetteIngram) <a href="https://twitter.com/MsYvetteIngram/status/1318618435442647043?ref_src=twsrc%5Etfw">October 20, 2020</a></blockquote> <script async src="https://platform.twitter.com/widgets.js" charset="utf-8"></script>
                     </li>
                     <li>
                     <blockquote class="twitter-tweet"><p lang="en" dir="ltr">Netflix Clone developed with React and firebase. Live demo: <a href="https://t.co/7kQiftpYzn">https://t.co/7kQiftpYzn</a> <a href="https://twitter.com/cleverqazi?ref_src=twsrc%5Etfw">@cleverqazi</a></p>&mdash; Yvette Ingram (@MsYvetteIngram) <a href="https://twitter.com/MsYvetteIngram/status/1318619926391853057?ref_src=twsrc%5Etfw">October 20, 2020</a></blockquote> <script async src="https://platform.twitter.com/widgets.js" charset="utf-8"></script>
                     </li>
                  </ul>
		         </div>
            </aside>
      </div>
   </section>
    );
  }
}

 export default Contact;